@import url(https://fonts.googleapis.com/css?family=Nunito+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}


.navbar {
  position: absolute;
  width: 1440px;
  height: 70px;
  left: 0px;
  top: 0px;
}
.navBarView {
  position: absolute;
  width: 100%;
  height: 70px;
  left: 0px;
  top: 0px;
  background: #42005A;
  box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.25); }

.signIn {
  position: absolute;
  width: 90px;
  height: 30px;
  left: 1150px;
  top: 20px;
  color: white !important;
  background-color: #BD36EC !important;
  border-radius: 20px !important; }

.signUp {
  position: absolute;
  width: 90px;
  height: 30px;
  left: 1260px;
  top: 20px;
  background-color: #BD36EC !important;
  border-radius: 20px !important; }

.NewReviewView {
  padding-top: 70px;
  padding-bottom: 25px; }

.submit-new-review {
  color: #42005A;
  padding-top: 30px;
  text-align: center; }

.submit-form {
  padding-top: 35px; }
  .submit-form .buttons {
    width: 100%;
    display: inline-block;
    overflow: auto;
    white-space: nowrap;
    margin: 0px auto; }
  .submit-form .submit-input {
    padding-bottom: 20px;
    width: 100%; }
  .submit-form .ratings-field {
    padding-bottom: 20px; }
  .submit-form .ratings {
    text-align: left;
    color: #42005A; }
  .submit-form a {
    text-decoration: none; }
  .submit-form .error-message {
    color: red;
    text-align: right;
    display: block;
    padding-top: 1rem;
    font-size: .75rem; }
  .submit-form .star-ratings {
    text-align: left; }
    .submit-form .star-ratings .rating-header {
      width: 100%;
      color: #42005A;
      vertical-align: middle; }
    .submit-form .star-ratings .tooltip {
      vertical-align: middle; }


.HomePageView {
  background-color: #42005a;
  padding-top: 70px;
  height: 100vh;
  text-align: center; }
  .HomePageView a {
    text-decoration: none; }

.search-text {
  padding-top: 50px;
  color: white; }
  .search-text .course {
    color: #fab224; }

.or-text {
  color: white;
  font-family: 'Nunito Sans', sans-serif;
  font-weight: 600;
  font-size: 14px;
  padding-top: 150px;
  padding-bottom: 150px; }

